@import url("https://fonts.googleapis.com/css2?family=Lato:wght@300;400&display=swap");
:root {
  --dark: #4b5055;
  --light: #ffffff;
  --neutral-200: #f4f4f4;
  --neutral-400: #d8d8d8;
  --neutral-600: #828488;
  --feature-100: #edf2fe;
  --feature-700: #4578ec;
}

html,
body {
  height: 100%;
  overflow: hidden;
}

body {
  margin: 0;
  background-color: var(--feature-100);
}

#root {
  height: inherit;
  padding: 0.5rem;
  box-sizing: border-box;
}

p {
  font-weight: 300;
  color: var(--dark);
}

h1 {
  font-size: 1.5rem;
}

h2 {
  font-size: 1rem;
  text-transform: uppercase;
}

.App {
  font-family: "Lato", sans-serif;
  display: flex;
  flex-direction: column;
  height: inherit;
}

.flex-row {
  display: flex;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.flex-center {
  justify-content: center;
  align-items: center;
}

.flex-end {
  justify-content: flex-end;
}

.mdfy-selector:not(:last-child) {
  margin-right: 0.5rem;
}

.flex-item {
  flex: 1;
}

.mdfy-selector {
  flex: 1;
  max-width: 11rem;
}

.mdfy__value-container {
  padding: 0 0.5rem 0 0.5rem;
}

.mdfy__value-container .mdfy__values {
  display: flex;
}

.mdfy__single-value,
.mdfy__multi-value__label {
  padding: 0;
  display: flex;
  align-items: center;
}

.mdfy__values__plus {
  font-size: 1rem;
  font-weight: 300;
  color: var(--feature-700);
}

.mdfy__badge {
  padding: 0.25rem 0.5rem;
  border-radius: 1rem;
  color: var(--dark);
  background-color: var(--neutral-200);
  text-transform: uppercase;
  font-size: 0.75rem;
}

.mdfy__badge.mdfy__badge--active {
  color: var(--light);
  background-color: var(--feature-700);
}

.mdfy__menu-list {
  cursor: default;
}

.mdfy__menu-list .aggs-group {
  border-bottom: 1px solid var(--neutral-400);
}

.mdfy__menu-list .aggs-group > :first-child {
  transform: translateX(-0.25rem);
}

.mdfy__option {
  height: 2.5rem;
}

.mdfy__option:not(:last-child) {
  border-bottom: 1px solid var(--neutral-200);
}

.mdfy__option,
.mdfy__option--is-selected {
  font-weight: 300;
  color: var(--dark);
}

.mdfy__option > .label-group {
  display: flex;
  align-items: center;
}

.mdfy__single-value > svg,
.mdfy__option > .label-group > svg {
  margin-right: 0.25rem;
  color: var(--dark);
}
